.map {
    height: 500px;
    background: white;
    padding: 1rem;
    border-radius: 20px;
    margin-top: 16px;
    box-shadow: 0 0 0px -4px rgba(0, 0, 0, 0.5);

}

.map .leaflet-container {
    height: 100%;
}

.info-flag img {
    width: 100px;
    border-radius: 5px;
}

.info-flag {
    height: 80px;
    width: 100%;
    background-size: cover;
    border-radius: 8px;
}

.info-name {
    font-size: 20px;
    font-weight: bold;
}
.info-deaths,
.info-recovered,
.info-confirmed {
    font-size: 16px;
    margin-top: 5px;
}